import {UserType} from '../HW8'

type ActionType =
    | { type: 'sort'; payload: 'up' | 'down' }
    | { type: 'check'; payload: number }

export const homeWorkReducer = (state: UserType[], action: ActionType): any => { // need to fix any
    switch (action.type) {
        case 'sort': { // by name
            const stateCopy = [...state]
            stateCopy.sort(sortByName)
            return action.payload === 'up' ? stateCopy : stateCopy.reverse() // need to fix
        }
        case 'check': {

            return state.filter(user => user.age >= action.payload) // need to fix
        }
        default:
            return state
    }
}

function sortByName(item1:UserType, item2:UserType) {
    let x = item1.name.toLowerCase();
    let y = item2.name.toLowerCase();
    if (x < y) {
        return -1;
    }
    if (x > y) {
        return 1;
    }
    return 0;
}
