import React, {ButtonHTMLAttributes, ComponentPropsWithoutRef, DetailedHTMLProps} from 'react'
import s from './SuperButton.module.css'

//    , children        
// type DefaultButtonPropsType = DetailedHTMLProps<ButtonHTMLAttributes<HTMLButtonElement>,
//     HTMLButtonElement>

type DefaultButtonPropsType = ComponentPropsWithoutRef<'button'>

type SuperButtonPropsType = DefaultButtonPropsType & {
    xType?: string
}

const SuperButton: React.FC<SuperButtonPropsType> = (
    {
        xType,
        className,
        disabled,
        ...restProps //       restProps,    children
    }
) => {
    // const finalClassName = s.button
        // + (disabled
        //         ? ...
        //         : xType === 'red'
        //             ? ...
        // + (className ? ' ' + className : '') //    
    const finalClassName = `${s.button} ${disabled ? s.disabled : ''} 
    ${xType ? s[xType] : s.default} ${className ? className : ''}`

    return (
        <button
            disabled={disabled}
            className={finalClassName}
            {...restProps} //        (children  )
        />
    )
}

export default SuperButton
